import { useState } from "react";
import styled from "styled-components";

const Input = styled.input`
    border:0.1px solid gray;
    border-radius: 10px;
    width: 270px;
    font-size: 18px;
    padding: 5px;
`
const Button = styled.button`
    border : 0;
    border-radius: 10px;
    margin-left: 10px;
    width: 60px;
    height: 30px;
    color:white;
    background-color: #212529;
`

const CardForm = ({onUpdate})=>{
    const includesHangul = (text) => /[-|-|-]/i.test(text);
    const [value, setValue] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    
    const translate = (e)=>{
        const userValue = e.target.value; //input     .
        setErrorMessage("") //      .
        if(includesHangul(userValue)){
            setErrorMessage("You cannot input Korean!");
        }
        setValue(userValue.toUpperCase()); //  .
    }

    const create = (e)=>{
        e.preventDefault();
        setErrorMessage("")
        if(value === ""){
            setErrorMessage("It cannot be submitted with an empty value!")
            return; //return     onUpdate(value)     return .
        }
        onUpdate(value);
    }

    return(
        <>
            <form onSubmit={create}>
                <input
                    style={{
                        border:"0.1px solid gray",
                        borderRadius: "10px",
                        width: "270px",
                        fontSize: "18px",
                        padding: "5px"
                    }}
                    type="text"
                    value={value}
                    onChange={translate}
                    placeholder="Please English language."
                />
                <Button>Click</Button>
            </form>
            <p style={{margin : "10px" , color : "red"}}>{errorMessage}</p>
        </>

    )
}

export default CardForm;